.gpt3__blog-container__article {
    height: 100%;
    width: 100%;
    
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}
.gpt3__blog-container__article-image {
    height: 100%;
    width: 100%;
    background: var(--color-footer);
}
.gpt3__blog-container__article-image img{
    height: 100%;
    width: 100%;
}
.gpt3__blog-container__article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 1rem 1.5rem;
    height: 100%;
}
.gpt3__blog-container__article-content p{
    color: #FFF;
    font-family: var(--font-family);
    font-size: 11.649px;
    font-weight: 700;
    line-height: 34.947px;
}
.gpt3__blog-container__article-content h3{
    color: #FFF;
    font-family: var(--font-family);
    font-size: 25.11px;
    font-weight: 800;
    line-height: 30.287px;

    margin-bottom: 5rem;
    cursor: pointer;
}
.gpt3__blog-container__article-content p:last-child{
    cursor: pointer;
}
@media screen and (max-width : 550px) {
    .gpt3__blog-container__article-content h3 {
        font-size: 18px;
        line-height: 25px;
    }
    
}